.navigation {
  height: 80px;
  background-color: #2f3241;
  width: 100%;
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.06);
}

.menu-items {
  margin-top: 20px;
  font-size: 18px;
  text-align: center;
  margin-right: 20px;
  color: #49b675;
  transition: all 0.3s;
}

.menu-items:hover {
  color: #3c9761;
}

.menu-items:focus {
  text-decoration: none;
}

.menu {
  padding: 25px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 60px;
  background-color: rgb(255, 255, 255);
  border-top: 1px #eaecef solid;
}

.footer-container {
  margin-top: 6px;
  font-size: 30px;
  text-align: center;
}

.footer-links {
  color: #2f3241;
}

.footer-links:hover {
  color: #49b675;
}

.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transform: -webkit-translate(-50%, -50%);
          transform: -webkit-translate(-50%, -50%);
  transform: -moz-translate(-50%, -50%);
  transform: -ms-translate(-50%, -50%);
  font-size: 30px;
  color: #49b675;
}

body {
  font-family: "Major Mono Display", monospace;
  margin: 0;
}

a {
  font-family: "Major Mono Display", monospace;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

